@import "../../../styles/mixins.scss";
@import "../../../styles/placeholders.scss";
@import "../../../styles/vars.scss";

.products {
  @include block-model(0 0 60px 0, none, 0, 0);
  background-color: $light-color;
  overflow-x: hidden;

  @media screen and (min-width: 1290px) {
    @include block-model(0 0 80px 0, none, 0, 0);
  }

  &__list {
    @extend %flex-center;
    flex-wrap: wrap;
    gap: 30px;
    padding: 0;
  }

  &__title {
    text-align: left;
    width: 290px;
    @include font($dark-color, 1.33, 30px, 600);

    @media screen and (min-width: 440px) {
      width: 410px;
    }

    @media screen and (min-width: 768px) {
      width: 400px;
    }

    @media screen and (min-width: 1290px) {
      width: 520px;
      @include font($dark-color, 1.2, 40px, 600);
    }
  }

  &__titleBox {
    @extend %flex-center2;
    @include block-model(0, none, 0, 0 auto 40px auto);
    @include size(410px, auto);
    gap: 30px;

    @media screen and (min-width: 1200px) {
      @include size(850px, auto);
    }

    @media screen and (min-width: 1290px) {
      @include size(960px, auto);
    }

    @media screen and (min-width: 1320px) {
      @include size(1290px, auto);
    }
  }

  &__link {
    @extend %flex-center;
    gap: 10px;
    @include font($yellow-color, 1.26, 19px, 600);
    @extend %animation-of-button;

    padding: 10px !important;
    border-radius: 20px !important;
    display: inline-flex;

    transition: all 0.5s ease-in-out;

    & path {
      fill: #ffbc57;
    }

    &:hover:not(:active) {
      background-color: #ffbc57;
      color: #fff;

      & path {
        fill: #fff;
      }
    }
  }

  &__arrow {
    @include size(24px, 24px);
    fill: $yellow-color;

    & > path {
      transition: all 0.6s ease;
    }
  }

  &__item {
    position: relative;
    @include block-model(20px 20px 20px 20px, 1px solid #e3e2e2, 8px, 0);
    @include size(280px, 420px);
    background: #fff;
    box-shadow: -3px 4px 15px 0px rgba(0, 0, 0, 0.06);

    @media screen and (min-width: 440px) {
      @include size(410px, 488px);
    }

    @media screen and (min-width: 768px) {
      @include size(290px, 420px);
    }

    @media screen and (min-width: 1290px) {
      @include size(300px, 468px);
    }

    & img {
      @include size(260px, 220px);
      object-fit: cover;
      overflow: hidden;
      border-radius: 8px;
    }
  }

  img {
    margin-bottom: 20px;
  }

  &__size {
    @include block-model(0, none, 0, 0 0 10px 0);
    @include font($grey-color, 1.69, 13px, 300);
    text-align: left;
  }

  &__subtitle {
    margin: 10px 0 0 0;
    text-align: left;
    display: none;
    align-items: center;
    @include size(260px, 52px);
    @include font($dark-color, 1.26, 17px, 600);

    @media screen and (min-width: 1290px) {
      display: flex;
    }
  }

  &__stock {
    display: flex;
    align-items: center;
    @include block-model(0, none, 0, 10px 0 0 0);
    gap: 5px;
  }

  &__stockArrow {
    @include size(24px, 24px);
    fill: #82b09d;
  }

  &__stockText {
    @include font(black, 1.69, 13px, 300);
  }

  &__oldPrice {
    @extend %position1;
    @include size(auto, 22px);
    @include font(#e03d3d, 1.46, 15px, 400);
    text-decoration: line-through;

    @media screen and (min-width: 1290px) {
      bottom: 40px;
    }
  }

  &__price {
    @extend %position2;
    @include font($dark-color, 1.3, 26px, 600);

    @media screen and (min-width: 1290px) {
      bottom: 10px;
      @include font($dark-color, 1.4, 20px, 600);
    }
  }

  &__couple {
    @extend %position3;
    display: flex;
    flex-direction: row;
    gap: 10px;
  }

  &__button {
    background-color: transparent;
    border: none;
    cursor: pointer;

    @extend %animation-of-button;
  }

  &__likeIcon {
    @include size(40px, 40px);

    @media screen and (min-width: 440px) {
      @include size(70px, 70px);
    }

    @media screen and (min-width: 768px) {
      @include size(40px, 40px);
    }

    @media screen and (min-width: 1290px) {
      @include size(50px, 50px);
    }
  }

  &__basketIcon {
    @include size(40px, 40px);

    @media screen and (min-width: 440px) {
      @include size(70px, 70px);
    }

    @media screen and (min-width: 768px) {
      @include size(40px, 40px);
    }

    @media screen and (min-width: 1290px) {
      @include size(50px, 50px);
    }
  }

  &__lastButton {
    @extend %flex-center;
    @include block-model(8px 20px 8px 20px, none, 8px, 30px auto 0 auto);
    @include size(290px, 50px);
    gap: 15px;
    background-color: $yellow-color;

    @media screen and (min-width: 440px) {
      @include size(410px, 50px);
    }

    @media screen and (min-width: 1290px) {
      display: none;
    }
  }

  &__buttonIcon {
    @include size(24px, 24px);
    fill: #fff;
  }

  &__buttonText {
    @include font(#fff, 1.26, 19px, 600);
  }
}
